[
`
Map.prototype.fn = () => {
    console.log("我是全局方法");
}
mp.fn();
`,
`
const mpIter = mp.entries();
let tmp = mpIter.next();
while(!tmp.done){
    console.log("键："+tmp.value[0], "值："+tmp.value[1]);
    tmp = mpIter.next();
}
`,
`
let mp = new Map([['a', 'aa'], ['b', 'bb'], ['c', 'cc']]);
for(let [key, value] of mp){
    console.log(key, value);
}
`,
`
let mp = new Map([['a', 'aa'], ['b', 'bb'], ['c', 'cc']]);
mp.forEach((value, key, map) => {
    console.log(value, key, map);
})
`,
`
// 使用...语法可以将map键值对取出
let mp = new Map([['a', 'aa'], ['b', 'bb'], ['c', 'cc']]);
mp = new Map([...mp, ['b', 'bbb'], ['c', 'ccc'], ['d', 'dd']]);
console.log(mp); // {"a" => "aa", "b" => "bbb", "c" => "ccc", "d" => "dd"}
`
]
    };
